@import url(https://fonts.googleapis.com/css?family=Slabo+27px|Roboto|Source+Sans+Pro);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
}
html {
  scroll-behavior: smooth;
}
  

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.navigation {
    position: absolute;
    width: 100%;
    padding: 10px 0px;
    background: linear-gradient(rgba(10, 10, 10, 0.1), rgba(10, 10, 10, 0.1));
  }
  .primary-nav {
    margin-top: 8px;
  }

  .primary-nav li {
    display: inline-block;
    margin: 0px 20px;
    font-size: 15pt;
    color: rgb(51, 49, 49);
    padding-bottom: 5px;
    transition: border 50ms linear;
  }
  .primary-nav li:hover {
    text-decoration: none;
    font-weight:bold;
    cursor: pointer;
    color: rgb(51, 49, 49);
  }
  
.main {
  background: linear-gradient(rgba(250, 250, 250, 0.1), rgba(250, 246, 250, 0.1)),
    url(/portfolio/static/media/home.983cbc9a.jpg); 
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  padding: 14.7% 0; 
  color: rgb(51, 49, 49);
}
.text-center {
  margin-top: 50px;
}
#hello, #name {
  font-size: 41px;
}
.about {
    padding: 3% 0;
    background: linear-gradient(
      rgba(175, 170, 170, 0.7),
      rgba(173, 164, 164, 0.7)
    ),
      url(/portfolio/static/media/about.13319e5f.jpg);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center center;
  }
  
  .about-header h1 {
    font-family: "Source Sans Pro", Arial, sans-serif;
    font-size: 34pt;
  }
  
  .about-header-quote {
    font-family: "Slabo", Helvetica, serif;
    font-style: italic;
    margin-bottom: 50px;
  }
  
  .about-general-description,.about-statistic-description {
    display: flex;
    justify-content: flex-start;
    max-width: 100%;
    margin-left: 15%;
  }
  
  .about-general-description h2,
  .about-statistic-description h2 {
    font-family: "Source Sans Pro", Arial, sans-serif;
    font-size: 24pt;
    margin-bottom: 20px;
  }
  
  .about-general-description p,
  .about-statistic-description p {
    font-family: "Roboto", Helvetica, serif;
    font-size: 12pt;
    margin-bottom: 20px;
  }
  
  .about-statistic-description p {
    margin: 5px 0px;
  }
  .about-option-buttons {
    display: flex;
    justify-content: center;
  }
  .about-option-buttons .btn-black-border{
    background-color: transparent;
    width: 100%;
    margin-top: 50px;
    margin-bottom: 50px;
    color: black;
    font-family: "Roboto", Helvetica, serif;
    font-size: 16pt;
    border-radius: 0;
    border: 2px solid black;
    transition: all 200ms ease-out;
  }
  
  .about-option-buttons .btn-black-border:hover{
    border: 2px solid #464646;
    background-color: white;
    color: white;
  }
  
.summary {
    background: linear-gradient(rgba(250, 250, 250, 0.1), rgba(250, 246, 250, 0.1)),
      url(/portfolio/static/media/project.f5644f94.jpg);
    background-attachment: fixed;
    background-attachment: center center;
    color: white;
  }
  
  .summary .row {
    padding: 10px;
    margin:10% 20%;
    width: 80%;
  }
  
  .summary h3 {
    font-family: "Source Sans Pro", Arial, sans-serif;
  }
  .summary p {
    font-family: "Roboto", Helvetica, serif;
  }

  .summary .description {
    margin: 10px;
  }
  
  
.carousel-container {
    position: relative;
    width: 65%;
    height: 600px;
    margin-left:17%;
  }
.carousel-container .carousel
.slide img {
                height: 100%;
            }
.projects {
  background: linear-gradient(rgba(212, 212, 212, 0.5), rgba(207, 205, 207, 0.5)),
    url(/portfolio/static/media/project.f5644f94.jpg);
  background-attachment: fixed;
  background-attachment: left center;
  color: white;
  padding: 60px 30px;
}
.banner-buttons .btn-border,
  .projects .btn-border {
    background-color: transparent;
    min-width: 100%;
    margin-top: 40px;
    color: rgb(51, 49, 49);
    font-family: "Roboto", Helvetica, serif;
    font-size: 1.2em;
    border-radius: 0;
    border: 2px solid rgb(51, 49, 49);
    transition: all 200ms ease-out;
  }
  
  .banner-buttons .btn-border:hover,
  .projects .btn-border:hover {
    border: 2px solid rgb(51, 49, 49);
    background-color: white;
  }
  
  
  .projects-header-quote {
    margin-bottom: 20px;
  }
  .projects h1,
  .projects h3 {
    font-family: "Source Sans Pro", Arial, sans-serif;
  }
  .projects p {
    font-family: "Roboto", Helvetica, serif;
    padding: 0 10px;
  }
  
  .projects-header-quote {
    font-style: italic;
  }
  
  
  
 
.contact {
    background: linear-gradient(rgba(10, 10, 10, 0.6), rgba(10, 10, 10, 0.6)), url(/portfolio/static/media/project.f5644f94.jpg);
    color: white;
    padding: 5%;
    background-size: cover;
  }
  
  .contact p {
    font-family: "Roboto", Helvetica, serif;
  }
  .contact-header {
    margin: 80px;
  }
  .contact-footer {
    padding: 10px;
    margin-top: 200px;
  }
  .contact-footer p{
    font-size: 50px;
    font-family: cursive;
  }
  .cantact-ways {
    text-align: center;
  }
  .contact-header-quote {
    display: flex;
    justify-content: center;
    margin: 5px;
  }
  .contact-header-quote img {
    padding: 3px;
    margin-right: 15px;
  }
  .contact-header-quote h3 {
    margin: 5px 0;
  }
  
  
  
