.App {
  text-align: center;
}

@import url("https://fonts.googleapis.com/css?family=Slabo+27px|Roboto|Source+Sans+Pro");
html {
  scroll-behavior: smooth;
}
  

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

